.card {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  align-items: flex-start;
  gap: 10px;
  margin-left: 100px;
  width: 420px;
  padding: 10px;
  background-color: #f3f3f3;
  border-radius: 8px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.title {
  color: darkgreen;
}

.text {
  padding-top: 25px;
  color: darkblue;
}

.field {
  width: 100%;
  height: 25px;
  /* border-color: aquamarine; */
  /* border: none; */
  color: darkblue;
  padding: 0 5px;
}

.btn {
  border: none;
  background-color: #4b51ef;
  border-radius: 5px;
  padding: 5px 15px;
  cursor: pointer;
  /* font-family: "Roboto", sans-serif;
    font-style: normal; */
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}